import styled, { css } from 'styled-components';
import theme from '../theme';

export const StyledTypographyComponent = styled.div`
  font-family: 'Rubik', sans-serif;
  margin-bottom: 10px;
  color: ${theme.textLinkColor};

  ${({ type }) => {
    switch (type) {
      case 'h1':
        return css`
          font-size: 48px;
          font-weight: 800;
          height: 50.3984px;
          line-height: 50.4px;
        `;
      case 'h2':
      case 'h3':
      case 'h4':
      case 'h5':
        return css`
          font-size: 1.8em;
          font-weight: bold;
        `;
      default:
        return '';
    }
  }}
`;
